<template>
    <div>
        <Title :title="title" back="true" exim="true" />

        <b-container fluid class="p-4">
            <b-row>
                <b-col cols="12" xl="8">
                    <!-- search bar -->
                    <serch-bar @filterChange="onFilterChange" :filter="searchFilter" />
                    <!-- filters -->
                    <Filters
                        :filterList="filtersList"
                        :filterModels="filterModels"
                        v-if="filtersList.length != 0"
                        @applyFilters="onApplyFilters"
                    />
                    <!-- table -->
                    <Table
                        :items="items"
                        :fields="fields"
                        :fieldsModels="fieldsModels"
                        :actionList="actionsList"
                        :filter="searchFilter"
                        :isBusy="isBusy"
                        :perPage="perPage"
                        :currentPage="currentPage"
                        :name="name"
                    />
                    <!-- pagination -->
                    <Pagination
                        :rows="rows"
                        :perPage="perPage"
                        :currentPage="currentPage"
                        :fields="fields"
                        :fieldsModels="fieldsModels"
                        @pageChange="onPageChange"
                        @addNewRow="onAddNewRow"
                    />
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
/*eslint no-unused-vars: ["error", { "argsIgnorePattern": "^_" }]*/
import API from "../api/ApiUtils.js";
import Title from "@/components/Title.vue";
import SerchBar from "../components/tables/SerchBar.vue";
import Filters from "../components/tables/Filters.vue";
import Table from "@/components/tables/Table.vue";
import Pagination from "../components/tables/Pagination.vue";

export default {
    components: { Title, SerchBar, Filters, Table, Pagination },
    props: ["name", "id", "parent"],
    data() {
        return {
            API: new API(this),
            title: "",
            searchFilter: null,
            isBusy: false,
            perPage: 10,
            currentPage: 1,
            items: [],
            fields: [],
            fieldsModels: {},
            filtersList: [],
            filterModels: {},
            filtersState: {},
            isFiltered: false,
            actionsList: [],
        };
    },
    methods: {
        getTableInfo() {
            this.filtersList = [];
            this.actionsList = [];
            this.filtersState = {};
            this.API.getTableInfo(this.name).then((data) => {
                let tableInfo = data;
                this.title = tableInfo.title;
                this.fields = tableInfo.fields;
                if (tableInfo.filters.length > 0) {
                    this.filtersList = tableInfo.filters;
                    // this.filtersList.forEach((filter, i) => {
                    //     filter.disabled = i == 0 ? false : true;
                    // });
                    tableInfo.filters.forEach((filter, i) => {
                        this.filtersState[filter.key] = "";
                        this.$set(this.filtersList[i], "disabled", i == 0 ? false : true);
                    });
                }
                if (tableInfo.actions.length > 0) {
                    this.actionsList = tableInfo.actions;
                }
                this.fields.push({ key: "actions", label: "Действия" });
                this.actionsList.push({ action: "delete" });

                this.getFieldsModels();
                this.getFilterModels();
            });
        },
        getItems(params) {
            this.isBusy = true;
            this.API.getData(this.name, params).then((data) => {
                this.items = data;
                this.isBusy = false;
            });
        },
        getFieldsModels() {
            this.fieldsModels = {};
            this.selectableFields.forEach((field) => {
                this.API.getData(field.key).then((data) => {
                    this.$set(this.fieldsModels, field.key, data);
                });
            });
        },
        getFilterModels() {
            this.filterModels = {};
            this.filtersList.forEach((filter) => {
                this.API.getData(filter.key).then((data) => {
                    this.$set(this.filterModels, filter.key, data);
                });
            });
        },
        onApplyFilters({ _id, fields }) {
            let current = this.filtersList.findIndex((x) => x.key == fields.field);
            if (current != this.filtersList.length - 1) {
                this.$set(this.filtersList[current + 1], "disabled", false);
            }

            this.isFiltered = true;
            this.filtersState[fields.field] = fields.value;
            let params = {
                mode: "advanced_filters",
            };
            for (let filter in this.filtersState) {
                params[filter] = this.filtersState[filter];
            }
            this.getItems(params);
        },
        onFilterChange(newValue) {
            this.searchFilter = newValue;
        },
        onPageChange(newCurrentPage) {
            this.currentPage = newCurrentPage;
        },
        onAddNewRow(item) {
            console.log("emit add", item);
            this.API.addRow(this.name, item).then((data) => {
                if (this.isFiltered) {
                    let params = {};
                    for (let state in this.filtersState) {
                        params[state] = this.filtersState[state];
                    }
                    this.getItems(params);
                }
                this.items.push(data[0]);
            });
        },
        init() {
            this.isFiltered = false;
            this.getTableInfo();
            this.getItems();
            let params = {};
            if (this?.parent) {
                params = {
                    [this?.parent]: this?.id,
                };
            }
            this.getItems(params);
        },
    },
    computed: {
        rows() {
            return this.items.length;
        },
        selectableFields() {
            let a = [];
            this.fields.forEach((field) => {
                if (Object.prototype.hasOwnProperty.call(field, "type")) {
                    if (field.type == "selectable") {
                        a.push(field);
                    }
                }
            });
            return a;
        },
    },
    mounted() {
        this.init();
    },
    watch: {
        name: function () {
            this.init();
        },
    },
};
</script>
