<template>
    <div>
        <b-button @click="showFieldModal($event.target)" :disabled="disabled" class="w-100">
            {{ labelLocal == "" ? label : labelLocal }}
        </b-button>

        <b-modal :id="fieldModal.id" :title="fieldModal.title" @hide="resetFieldModal" centered>
            <b-table
                :id="tableId"
                :items="items"
                :fields="fields"
                :per-page="perPage"
                :current-page="currentPage"
                sort-icon-left
                bordered
                responsive
                :filter="filter"
                @filtered="onFiltered"
            >
                <template #cell(actions)="row">
                    <b-button variant="primary" @click="selectRow(row.item)"> Выбрать </b-button>
                </template>
            </b-table>

            <b-pagination
                v-model="currentPage"
                :total-rows="rows"
                :per-page="perPage"
                first-number
                last-number
                :aria-controls="tableId"
            ></b-pagination>

            <!--  -->
            <template #modal-footer="{ ok, cancel }">
                <b-button variant="danger" @click="cancel()"> Отмена </b-button>
                <b-button class="d-none" variant="success" @click="addNewRow(ok)"> OK </b-button>
            </template>
        </b-modal>
    </div>
</template>

<script>
let uid = 0;

export default {
    props: ["label", "model", "rowId", "items", "disabled"],
    data() {
        uid += 1;
        return {
            fieldModal: {
                id: `field-modal-${uid}`,
                title: "Выберите из списка",
            },
            tableId: `table-modal-${uid}`,
            perPage: 10,
            currentPage: 1,
            filter: null,
            labelLocal: this.label,
        };
    },
    methods: {
        showFieldModal(button) {
            this.$bvModal.show(this.fieldModal.id, button);
        },
        onFiltered(filteredItems) {
            this.rows = filteredItems.length;
            this.currentPage = 1;
        },
        resetFieldModal() {},
        selectRow(item) {
            let fields = {
                field: this.model,
                value: parseInt(item.id),
            };
            this.$emit("updateField", {
                id: this.rowId,
                fields: fields,
            });
            this.$emit("labelChange", {
                id: parseInt(item.id),
                model: this.model,
            });
            this.$bvModal.hide(this.fieldModal.id);
        },
    },
    computed: {
        fields() {
            return [
                { key: "name", sortable: false },
                { key: "actions", label: "Actions" },
            ];
        },
        rows() {
            return this.items ? this.items.length : 0;
        },
    },
};
</script>
