<template>
    <b-row class="mb-4">
        <b-col>
            <b-row>
                <b-col v-for="item in filterList" :key="item.id" cols="12" class="mb-2">
                    <router-link
                        :to="{
                            name: 'dict',
                            params: {
                                name: item.key,
                                title: item.multiple,
                            },
                        }"
                    >
                        <b-button class="w-100">{{ item.multiple }}</b-button>
                    </router-link>
                </b-col>
            </b-row>
        </b-col>
        <b-col cols="8" class="text-end">
            <b-row>
                <b-col v-for="(item, index) in filterList" :key="item.id" cols="12" class="mb-2">
                    <!-- <filter-modal :label="item.label" :model="item.key" /> -->
                    <b-row>
                        <b-col cols="6" class="position-relative">
                            <label class="text-end lh-lg">{{ item.label }}:</label>
                        </b-col>
                        <b-col>
                            <change-field-modal
                                :ref="`filter-field-${item.key}`"
                                :label="'Не выбранно'"
                                :model="item.key"
                                :disabled="filterList[index].disabled"
                                rowId="-1"
                                :items="filterModels[item.key]"
                                @updateField="onUpdateField"
                                @labelChange="onLabelChange"
                            />
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
        </b-col>
    </b-row>
</template>

<script>
import ChangeFieldModal from "./ChangeFieldModal.vue";

export default {
    components: { ChangeFieldModal },
    props: ["filterList", "filterModels"],
    data() {
        return {};
    },
    methods: {
        onUpdateField: function ({ id, fields }) {
            this.$emit("applyFilters", { id, fields });
        },
        onLabelChange({ id: itemId, model }) {
            this.$refs[`filter-field-${model}`][0].labelLocal = this.getFieldById(
                itemId,
                model,
                "name"
            );
        },
        getFieldById(id, model, field) {
            return this.filterModels[model].find((x) => x.id == id)[field];
        },
    },
    watch: {
        filterList: function (newValue) {
            console.log("change", newValue);
        },
    },
};
</script>
